/*
	Content object

	This is the container for what will essential be the meat of the app,
	id="app-viewport", if you will. It will hold the styling for the content
	box and it's variety of combinations with info bars, sidebars etc.
   ========================================================================== */

.content {
  margin-top: $large-spacing-unit;
}

%content__main,
.content__main {
  min-height: calc(100vh - #{$primary-nav-height} - #{$primary-nav-height} - #{$total-nav-height}); //100 viewport, minus the padding and the navigation to give the appearance of full screen.
  background-color: #ffffff;
  border: $brand-border;
  border-radius: $brand-rounding;
  padding: 0;

  &--has-sidebar {
    border-left: none;
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
  }

  &--has-infobar {
    border-right: none;
    border-bottom-right: 0;
    border-top-right-radius: 0;
  }
}

.main-content__header {
  padding: 17px $base-spacing-unit;
}


