}.ko-loader-630546ca{& {
  display: block;
  position: relative;
  width: 12px;
  height: 15px;
  margin: 0;
  padding: 0;
}

.ko-loader__item {
  display: block;
  width: 2px;
  height: 15px;
  background-color: #CCCCCC;
  position: absolute;
  margin: 0;
  padding: 0;
  animation: ko-loader__state1 1.1s cubic-bezier(0.645, 0.045, 0.355, 1.000) 0s infinite alternate;

  &:nth-child(2) {
    left: 4px;
    animation-name: ko-loader__state2;
    animation-timing-function: ease-in-out;
  }

  &:nth-child(3) {
    left: 8px;
    animation-name: ko-loader__state3;
  }
}

// TODO would love to split this, but ember-component-css prevents us from doing that.
&.ko-loader--large {
  width: 24px;
  height: 30px;

  .ko-loader__item {
    width: 4px;
    height: 30px;

    &:nth-child(2) {
      left: 8px;
    }

    &:nth-child(3) {
      left: 16px;
    }
  }
}

@keyframes ko-loader__state1 {
  0%   { transform: scaleY(1); }
  50%  { transform: scaleY(.3); }
  100% { transform: scaleY(1); }
}

@keyframes ko-loader__state2 {
  0%   { transform: scaleY(.5); }
  25%  { transform: scaleY(.9); }
  50%  { transform: scaleY(.5); }
  75%  { transform: scaleY(.9); }
  100% { transform: scaleY(.5); }
}

@keyframes ko-loader__state3 {
  0%   { transform: scaleY(.3); }
  50%  { transform: scaleY(1); }
  100% { transform: scaleY(.3); }
}
