}.ko-universal-search--entry-d5701d7c{@import '../../app/components/ko-universal-search/variables';

& {
  $border-color: #EBEEF0;
  $instruction-width: 150px;
  $highlight-color: #E5E5EE;
  display: inline-block;
  width: calc(100% - #{$total-icon-size + 300px});  // smashed in 300 px for get help and X - I have a note from Tom
  /*margin-left: -3px; //I don't know where these extra 3px come from?*/
  font-size: 16px;
  line-height: 40px;
  padding: 0 $half-spacing-unit 0 42px;
  border: none;
  outline: none;
}
}.ko-universal-search--result-1711775f{@import '../../app/components/ko-universal-search/variables';

&.universal-search_result {
  line-height: $line-height;

  &--highlighted {
    background-color: $highlight-color;

    .universal-search_result-instruction {
      color: #5083DB;
    }
  }

  // only the first icon from each results group should be displayed
  &:not(:first-of-type) .universal-search__icon {
    background: rgba(255,255,255,0);
  }

  &:last-of-type .universal-search__result-snippet {
    // the last item is within a ul, which has a full width border already
    border-bottom: none;
  }
}

.universal-search_result-snippet {
  // can't work out where the 3px comes from ?
  width: calc(100% - #{$total-icon-size + $instruction-width + 3px});
  border-bottom: 1px solid $border-color;
  display: inline-block;

  // em is passed back from the server
  // we can't change it, so just style it
  // sorry style guide!
  em {
    font-weight: 600;
    font-style: normal;
  }
}

.universal-search_result-instruction {
  color: #FFFFFF; // we want the div to maintain width (the result-snippet
  // div expects a div of this width) but we don't want to see
  // the text.
  width: $instruction-width;
  border-bottom: 1px solid $border-color;
  display: inline-block;
  font-style: italic;
}
