/*
    The container for the blocks that make up the site's top header.
    It will be made up of other modules: tab-nav, main-nav etc. Used to
    outline the structure for these to fit into.
   ========================================================================= */

.header {
  position: absolute;
  display: inline-block;
  width: 100%;
  vertical-align: top;
  top: 0;
  height: $total-nav-height;
  // Needs to be above content area for dropdowns etc.
  z-index: 1;
}

.header__title {
  margin: 0;
}

.header__subtitle {
  @extend %t-caption;
}

.header__image {
  height: 48px;
  width: 48px;
  border-radius: $brand-rounding;
}
