[class^="i-"], [class*=" i-"] {
  &:before {
    vertical-align: middle;
  }
}

.i-after {
  @include style-icon {
    @extend %u-v-center;
    right: $half-spacing-unit;
  }
}

.i-center {
  @include style-icon {
    @extend %u-center;
  }
}

.i-inherit-lh {
  line-height: inherit !important;  //override 3rd party css
}


/**
 * Icon colour helper classes
 */
.i-color-grey,
%i-color-grey {
  color: #BEC4C7;
}
.i-color-grey--dark,
%i-color-grey--dark {
  color: #5F6C73;
}

/**
 * Icon sizing helper classes
 */
.i-size-base,
%i-size-base {
  @include rem('font-size', $base-spacing-unit);
}
.i-size-20,
%i-size-20 {
  @include rem('font-size', 20px);
}
.i-size-14,
%i-size-14 {
  @include rem('font-size', 14px);
}
