/*
	Nav module
   ========================================================================== */

$primary-nav-height:            42px;
$secondary-nav-height:          42px;
$total-nav-height:              $primary-nav-height + $secondary-nav-height;
$nav-line-height:               $primary-nav-height - 2px; //offset the active border top


%nav-layout {
  height: 100%;
  width: 100%;
  line-height: $primary-nav-height;
}
%nav-item-layout {
  height: 100%;
  display: inline-block;
  padding: 0 $half-spacing-unit;
  border-top: 2px solid transparent;
  cursor: pointer;
}
%nav-is-active {
  background-color: #231D2A;
  border-top: 2px solid #4EAFCB;
}


.nav {
  @extend %nav-layout;
  height: $primary-nav-height;
  width: 100%;
  position: relative;
  background-color: #40364D;
}

.nav__items {
  height: 100%;
  width: 100%;
}

.nav__item {
  @extend %nav-item-layout;
  color: #b3b6ba;
  text-transform: uppercase;
  text-decoration: none;

  &.is-active {
    @extend %nav-is-active;
  }
}

.nav__image {
  height: $primary-nav-height;
  width: $primary-nav-height;
  display: inline-block;
  vertical-align: top;
  background: url('/images/brand/kayako-logo.svg') no-repeat center center;
}

//Main Nav
.nav-main {
  @extend %nav-layout;
  @extend %u-reset-lh;
}

.nav-main__item {
  @extend %nav-item-layout;
  @extend %i-size-20;
  color: #FFFFFF;
  line-height: 37px !important; //overiding generated stylesheet

  &.is-active {
    @extend %nav-is-active;
  }
}


//nav secondary
.nav-secondary {
  position: relative;
  background-color: #ffffff;
  border-bottom: $brand-border;
  height: $secondary-nav-height;
}


//nav tabs
.nav-tabs {
  @extend %nav-layout;
  line-height: $nav-line-height;
  border-left: $brand-border--dark;
}

.nav-tabs__item {
  @extend %nav-item-layout;
  float: left;
  position: relative;
  padding-right: $base-spacing-unit + $quarter-spacing-unit;
  border-right: $brand-border--dark;
  color: #b3b6ba;

  &.is-active {
    @extend %nav-is-active;
  }

  &:last-of-type {
    border-right: none;
  }
}

.nav-tabs__close {
  @extend %u-v-center;
  right: 2px;
  cursor: pointer;
}

