/**
* Adds an ellipsis once the content has reached the width passed in.
*/
@mixin truncate($max-width: 200px) {
  max-width: $max-width;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/**
 * Used to style icons when you're in its parent's selector.
 */
@mixin child-icon() {
  > [class*=" i-"]:before {
    @content;
  }
}

@mixin style-icon() {
  &[class*=" i-"]:before {
    @content;
  }
}
